.header {
  background-image: linear-gradient(
    to right top,
    #060537,
    #030a45,
    #020c52,
    #060d60,
    #0e0c6e
  );
  color: #fff;
  text-align: center;
  padding: 0.1rem;
}

.title {
  font-size: 2rem;
}

.input-container {
  display: flex;
  justify-content: center;
  margin-top: 3rem;
}

#gif-search-input {
  padding: 0.3rem 0;
  width: 350px;
  font-size: 1.3rem;
  outline: 0;
  border-color: rgba(blue, blue, blue, 0.7);
}

#gif-search-submit {
  width: 100px;
  font-size: 1.2rem;
  background-color: #01579b;
  color: #fff;
}

.gif-container {
  display: flex;
  justify-content: center;
  width: 80%;
  flex-wrap: wrap;
  margin: 3rem auto 0;
}

.close {
  font-size: 1.5rem;
  position: absolute;
  right: 0;
  top: 0;
  cursor: pointer;
  margin-right: 1rem;
  margin-top: 1rem;
  background-color: #01579b;
  color: #fff;
}

.gif-focus-display {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.7);
}

.about-container{
  text-align: center;
  width: 50%;
  margin: auto;
}

.about-container p {
  font-size: 1.5rem;
}

.navbar {
  display: flex;
  list-style-type: none;
  justify-content: center;
  margin: 2rem auto;
  /* border-bottom: 3px solid black; */
  padding-bottom: 1rem;
  width: 35%;
}

.navbar li {
  margin-right: 1rem;
}

.navbar a {
  color: black;
  text-decoration: underline;
}

a.active-page {
  background-color: gray;
  color: white;
  padding: 5px;
}

